import axios from "axios";
import React, { Component } from "react";
import { Link } from "react-router-dom";

class EstudioIndex extends Component {
  state = { estudios: [], isAdminView: false };

  async componentDidMount() {
    const response = await axios.get("/_estudios");
    console.log(response);
    this.setState({
      estudios: response.data.estudios,
      isAdminView: response.data.isAdminView,
    });
  }

  createEditor = () => {
    this.props.history.push("/estudios/new");
  };

  render() {
    const estudios = this.state.estudios.map((estudio, i) => {
      const url = `/estudios/${estudio._id}`;
      return (
        <li key={i} className="devocional-li">
          <Link to={url}>{estudio.title}</Link>
        </li>
      );
    });

    return (
      <center>
        {this.state.isAdminView === true ? (
          <button onClick={this.createEditor} className="btn btn-success mb-3">
            Crear Nuevo Devocional
          </button>
        ) : (
          ""
        )}
        <div className="EstudioIndex">
          <h2>Devocionales</h2>
          <ul>{estudios}</ul>
        </div>
      </center>
    );
  }
}

export default EstudioIndex;
